"use client";

import React from "react";
import { useNavigate } from "@tanstack/react-router";

import { Dock, DockIcon } from "@/components/magicui/dock";
import { currentFileContentAtom } from "@/features/docs/store/docs.store";
import { useAtomValue } from "jotai";
import {
  Copy,
  Code,
  GitBranch,
  HelpCircle,
  BookOpen,
  Compass,
  Library,
  Terminal,
} from "lucide-react";
import { toast } from "sonner";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "./ui/tooltip";

export type IconProps = React.HTMLAttributes<SVGElement>;

export function AppDock() {
  const currentFileContent = useAtomValue(currentFileContentAtom);
  const navigate = useNavigate();

  function copyCode() {
    if (currentFileContent) {
      navigator.clipboard.writeText(currentFileContent);
      toast.success("Código copiado para a área de transferência");
    }
  }

  function goToStack() {
    navigate({ to: "/docs/stack" });
  }

  console.log("AppDock");
  return (
    <div className=" fixed z-50 right-0 left-0 bottom-4">
      <TooltipProvider delayDuration={100}>
        <Dock direction="middle" className="h-14">
          <DockIcon>
            <Icons.copyCode className="size-5" onClick={copyCode} />
          </DockIcon>
          <DockIcon>
            <Icons.stack className="size-5" onClick={goToStack} />
          </DockIcon>
          <DockIcon>
            <Icons.cli className="size-5" />
          </DockIcon>
          <DockIcon>
            <Icons.git className="size-5" />
          </DockIcon>
          <DockIcon>
            <Icons.faq className="size-5" />
          </DockIcon>
          <DockIcon>
            <Icons.docs className="size-5" />
          </DockIcon>
          <DockIcon>
            <Icons.guides className="size-5" />
          </DockIcon>
          <DockIcon>
            <Icons.resources className="size-5" />
          </DockIcon>
          {/* <DockIcon className="mx-2 w-min"> */}
          {/* <DockProfileButton /> */}
          {/* </DockIcon> */}
          {/* <DockIcon>
            <Icons.googleDrive className="size-6" />
          </DockIcon>
          <DockIcon>
            <Icons.notion className="size-6" />
          </DockIcon>
          <DockIcon>
            <Icons.whatsapp className="size-6" />
          </DockIcon> */}
        </Dock>
      </TooltipProvider>
    </div>
  );
}

